@font-face {
  font-family: "TT Commons";
  src: local("TT Commons Regular"), local("TTcommons-regular"), url("../fonts/TTCommons-Regular.woff2") format("woff2"), url("../fonts/TTCommons-Regular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "TT Commons";
  src: local("TT Commons Medium"), local("TTcommons-medium"), url("../fonts/TTCommons-Medium.woff2") format("woff2"), url("../fonts/TTCommons-Medium.woff") format("woff");
  font-weight: 500;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "TT Commons";
  src: local("TT Commons Bold"), local("TTcommons-bold"), url("../fonts/TTCommons-Bold.woff2") format("woff2"), url("../fonts/TTCommons-Bold.woff") format("woff");
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}
html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  min-width: 360px;
  font-family: "TT Commons";
  font-size: 16px;
  line-height: 20px;
  color: #202020;
}

img {
  max-width: 100%;
  height: auto;
}

a {
  text-decoration: none;
  color: inherit;
}

button {
  cursor: pointer;
}

.visually-hidden:not(:focus):not(:active),
input[type=checkbox].visually-hidden,
input[type=radio].visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.header__container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 1280px;
  margin: 0 auto;
  min-width: 360px;
  padding-top: 20px;
  padding-left: 40px;
  padding-bottom: 20px;
  padding-right: 40px;
}

.header__logo-image {
  vertical-align: middle;
}

@media (max-width: 1280px) {
  .header__container {
    width: auto;
  }
}
@media (max-width: 870px) {
  .header__container {
    padding-top: 5px;
    padding-left: 20px;
    padding-bottom: 5px;
    padding-right: 20px;
  }
}
.main-nav__list {
  width: 500px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 0;
  padding: 0;
  list-style: none;
}

.main-nav__list-link {
  padding-bottom: 2px;
  border-bottom: 2px solid transparent;
  font-weight: 500;
  font-size: 18px;
  color: #202020;
  line-height: 24px;
  transition: all 0.2s linear;
}
.main-nav__list-link:hover {
  border-bottom-color: #F1864B;
}
.main-nav__list-link:active {
  color: #F1864B;
  border-bottom-color: #F1864B;
}

.main-nav__button {
  display: none;
  width: 50px;
  height: 50px;
  border: none;
  background-color: transparent;
  background-size: contain;
  background-repeat: no-repeat;
  background-position: center;
}

.main-nav__button--closed {
  background-image: url("../../img/icon-closed.svg");
}

.main-nav__button--opened {
  background-image: url("../../img/icon-opened.svg");
}

@media (max-width: 870px) {
  .main-nav {
    order: 3;
  }
  .main-nav > .main-nav__list {
    display: none;
  }
  .main-nav--show > .main-nav__list {
    position: absolute;
    top: 80px;
    left: 0;
    right: 0;
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
  }
  .main-nav--show .main-nav__list-item {
    width: 100%;
    padding-top: 20px;
    padding-bottom: 20px;
    border-bottom: 1px solid #eeeeee;
    text-align: center;
  }
  .main-nav--show .main-nav__list-item:first-child {
    border-top: 1px solid #eeeeee;
  }
  .main-nav__button {
    display: block;
  }
}
.phones-list {
  display: flex;
  flex-direction: column;
  margin: 0;
  padding: 0;
  list-style: none;
}

.phones-list__link {
  font-weight: 500;
  font-size: 20px;
  line-height: 26px;
}

@media (max-width: 550px) {
  .phones-list__link {
    font-size: 18px;
    line-height: 24px;
  }
}
.promo__container {
  width: 1440px;
  min-height: 600px;
  padding-top: 200px;
  padding-right: 40px;
  padding-left: 40px;
  background-image: url("../../img/bg-promo-girl.png"), url("../../img/icon-bg-promo-right.svg");
  background-repeat: no-repeat;
  background-position: center right, right center;
}

.promo__title {
  width: 630px;
  margin: 0 auto;
  margin-bottom: 20px;
  padding: 0;
  font-weight: 700;
  font-size: 96px;
  line-height: 96px;
  text-align: center;
  color: #454848;
  text-transform: uppercase;
}

.promo__subtitle {
  margin: 0;
  bottom: 0;
  font-weight: 400;
  font-size: 32px;
  line-height: 36px;
  text-align: center;
  color: #454848;
}